import type { RendererTextureSet} from "@xeokit/scene";
import type {WebGLRendererTexture} from "./WebGLRendererTexture";


/**
 * @private
 */
export class WebGLRendererTextureSet implements RendererTextureSet {

    public readonly id: string;
    public readonly colorTexture: WebGLRendererTexture;
    public readonly metallicRoughnessTexture: WebGLRendererTexture;
    public readonly emissiveTexture: WebGLRendererTexture;
    public readonly occlusionTexture: WebGLRendererTexture;

    constructor(params: {
        id: string;
        colorTexture: WebGLRendererTexture;
        metallicRoughnessTexture: WebGLRendererTexture;
        emissiveTexture: WebGLRendererTexture;
        occlusionTexture: WebGLRendererTexture;
    }) {
        this.id = params.id;
        this.colorTexture = params.colorTexture;
        this.metallicRoughnessTexture = params.metallicRoughnessTexture;
        this.emissiveTexture = params.emissiveTexture;
        this.occlusionTexture = params.occlusionTexture;
    }
}
